.title {
  &__heading {
    margin-bottom: 16px;
    font-family: Poppins, sans-serif;
    font-style: normal;
    font-weight: 600;
    font-size: 32px;
    line-height: 150%;
    color: #253757;
  }

  &__text {
    max-width: 754px;
    font-family: "Open Sans", sans-serif;
    font-style: normal;
    font-weight: 600;
    font-size: 24px;
    line-height: 150%;
    color: #6c788b;
  }
}

@media screen and (max-width: 959px) {
  .title {
    &__heading {
      text-align: center;
    }

    &__text {
      margin-right: 10px;
      margin-left: 10px;
    }
  }
}
